.policy-summary {
  .summary-head {
    margin-bottom: 24px;
    font-family: Roboto Serif;
    font-size: 32px;
    font-weight: 400;
    line-height: 54px;
    letter-spacing: 0em;
    text-align: left;
  }

  .summary-content {
    background: #f2f7fc;
    padding: 16px;
    border-radius: 8px;
    margin-bottom: 24px;

    table {
      width: 100%;

      td {
        width: 25%;
        padding-bottom: 12px;
        font-size: 16px;

        .title {
          font-size: 18px;
        }
      }

      tbody {
        tr:last-child td {
          padding-bottom: 0;
        }
      }
    }
  }
}

.admin-summary-data {
  .tab-title {
    font-size: 30px;
    font-weight: 500;
    line-height: 45px;
    letter-spacing: 0em;
    text-align: left;
    margin-bottom: 16px;
  }

  .summary-detail-section {
    background: #f2f7fc;
    border-radius: 8px;
    margin-bottom: 16px;

    &.editable .summary-card {
      .card-body label {
        span {
          display: none;
        }
        input {
          display: block;
        }

        .input-sub-title {
          display: inline-block;
        }

        & + .select-group {
          display: block;
        }
      }

      .card-body > .row > * {
        width: 33.33%;
      }

      &.mortgagee-card .card-body > .row > * {
        width: 50%;
      }
    }

    .summary-card {
      padding: 16px;
      border-bottom: 1px solid #b3d3ec;

      &:last-child {
        border-bottom: 0;
      }
    }

    .card-header,
    .card-title,
    .card-body {
      all: revert;
    }

    .card-header {
      display: flex;
      justify-content: space-between;
      margin-bottom: 16px;

      .card-title {
        all: revert;
        font-size: 18px;
        font-weight: 500;
        line-height: 28px;
        letter-spacing: 0em;
        text-align: left;
      }
    }

    .card-body {
      table {
        th,
        td {
          padding: 0;
        }
      }
      label {
        margin-bottom: 0;
        span {
          display: block;
        }
        .input-value {
          font-weight: 400;
        }

        input {
          display: none;
        }

        & + .select-group,
        .input-sub-title {
          display: none;
        }
        .input-sub-title {
          padding: 4px 12px 0;
          font-size: 14px;
          font-weight: 400;
          line-height: 22px;
          text-align: left;
          color: #616161;
        }
      }
    }

    .location-card {
      .card-body {
        table {

          th,
          td {
            &.address-field {
              width: 40%;
            }
            padding-right: 15px;
            padding-bottom: 12px;
          }

          th:nth-child(1),
          th:nth-child(2),
          td:nth-child(1),
          td:nth-child(2) {
            width: 60px;
            padding-right: 10px;

            input {
              padding: 0 5px;
              text-align: center;
            }
          }
        }
      }
    }
  }
}
